html {
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

// adaptive images
img {
    max-width: 100%;
    height: auto;
    vertical-align: top;
}

.MuiInputBase-input {
    @include placeholder {
        color: $red !important;
    }
    @include placeholder {
        color: $placeholder-color !important;
        font-weight: 400 !important;
        font-size: 14px !important;
        opacity: 1 !important;
    }
}

input[type='text'],
input[type='tel'],
input[type='email'],
input[type='search'],
input[type='password'],
textarea {
    -webkit-appearance: none;
    -webkit-border-radius: 0;
    width: 100%;
    outline: none;
    box-sizing: border-box;
    color: $black;
    padding: 12px 16px;
    border: 1px solid $light-blue-300;
    border-radius: 8px;
    transition: $animation-speed border-color ease-out,
                $animation-speed color ease-out;

    &:focus {
        border-color: $blue;
    }

    &:focus {
        border-color: $blue;
    }

    .Mui-error & {
        color: $red;
        border-color: $red;
    
        @include placeholder {
            color: $red !important;
        }

        &:focus {
            border-color: $blue;

            @include placeholder {
                color: $placeholder-color !important;
            }
        }
    }

    &.error {
        color: $red;
        border-color: $red;
    
        @include placeholder {
            color: $red;
        }
    }

    @include placeholder {
        color: $placeholder-color;
        font-weight: 400;
        font-size: 14px;
    }
}