@import '../../../styles/base/variables';

.progressHeading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;

    @media screen and (max-width: $phone) {
        flex-wrap: wrap;
        flex-direction: column;
        align-items: flex-start;
    }
}

.counter {
    font-weight: 700;
    font-size: 14px;
    color: $blue;
    text-transform: uppercase;

    @media screen and (max-width: $phone) {
        margin-bottom: 5px;
    }
}

.label {
    font-weight: 500;
    color: $gray;
    font-size: 16px;
}

.progressBar {
    background: $light-blue-300;
    overflow: hidden;
    height: 4px;
    border-radius: 8px;
}

.progressLine {
    background: $blue;
    height: inherit;
    transition: $animation-speed width ease-out;
    border-radius: 8px;
}