@import '../../styles/base/variables';

.fieldResponse {
    color: $gray;
    font-size: 14px;
    line-height: 22px;
    margin: 0;

    @media screen and (max-width: $tablet) {
        margin-bottom: 0px!important;
        font-size: 12px;
    }

    &:not(:last-of-type) {
        margin-bottom: 6px;
    }

    .fieldResponseValue {
        color: #3D3E45;
    }
}