/* width */
::-webkit-scrollbar {
    width: $width-scrollbar;
    height: $height-scrollbar;
}

/* Track */
::-webkit-scrollbar-track {
    background: $light-blue-200;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: $blue;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: $blue;
}