@import '../../styles/base/variables';

.title {
    font-weight: 700;
    font-size: 18px;
    text-transform: uppercase;
    color: $blue;
    margin-bottom: 20px;
}

.questionList {

    @media screen and (max-width: $desktop) {
        display: flex;
        justify-content: flex-start;
        overflow: auto;
        white-space: nowrap;
    }

    li {
        margin-bottom: 10px;

        @media screen and (max-width: $desktop) {
            margin: 0 5px 15px 0;
        }
    }
}