@import '../../styles/base/variables';

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba($black-200, .5);
    z-index: 400;
}

.modalWrap {
    width: 100vw;
    height: 100vh;
    z-index: 500;
    position: fixed;
}

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: $white;
    border-radius: 12px;
    z-index: 600;
    max-width: 571px;

    
    @media screen and (min-width: $tablet) {
        min-width: 428px;
    }

    @media screen and (max-width: $tablet) {
        width: calc(100% - 25px);
    }
}

.modalHeader {
    text-align: center;
    padding: 32px 45px 24px;
    border-bottom: 1px solid $modal-header-border ;

    @media screen and (max-width: $tablet) {
        padding: 15px;
    }
}

.modalActions {
    padding: 20px;
    li {
        button {
            width: 100%;
        }
        &:not(:last-child) {
            margin: 0 0 12px;
        }
    }
}