@import '../../styles/base/variables';

.dragndropWrapper {
    padding: 20px;
    border: 1px solid $light-blue-300;
    border-radius: 12px;
}

.dragndropWrapperActive {
    display: flex;
    padding: 20px 40px 20px 20px;

    @media screen and (max-width: $tablet) {
        flex-wrap: wrap;
        padding: 15px;
    }

    .dragndropArea {
        width: 50%;

        @media screen and (max-width: $widescreen) {
            max-width: 379px;
        }

        @media screen and (max-width: $tablet) {
            max-width: none;
            width: 100%;
            margin-bottom: 20px;
        }
    }
}


.filesBlock {
    width: 50%;
    max-height: 215px;
    overflow-y: auto;
    padding: 0 15px 0 40px;

    @media screen and (max-width: $widescreen) {
        width: 100%;
    }

    @media screen and (max-width: $tablet) {
        width: 100%;
        padding: 0;
    }
}

.filesTtems {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #0F0F0F;
    font-weight: 400;
    font-size: 12px;
    padding: 9px 10px;
    border: 1px solid $green;
    border-radius: 8px;

    &:not(:last-child) {
        margin: 0 0 10px;
    }
}

.fileNname {
    padding-right: 15px;
}

.removeFile {
    cursor: pointer;
    background: none;
    border: 0;
    background: #FFF3F3;
    border-radius: 50%;
    padding: 0;
    width: 20px;
    height: 20px;
}

.resultTitle {
    font-size: 14px;
    font-weight: 700;
    color: $gray;
    text-transform: uppercase;
    margin: 0 0 12px;
}

.dragndropSubtitle {
    font-weight: 400;
    font-size: 12px;
    color: $gray;
}

.dragndropTitle {
    font-weight: 700;
    font-size: 16px;
    color: #1D1D20;
    margin: 25px 0 10px;

    a {
        color: $blue;
        text-decoration: underline;
        cursor: pointer;
    }
}


.dragndropArea {
    display: flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 40px 20px;
    border: 1px dashed rgba(3, 28, 119, 0.3);
    border-radius: 10px;
    background: #FBFCFF;
    text-align: center;
}

.signWrap {
    width: 100%;
    position: relative;
    border: 1px solid $light-blue-300;
    border-radius: 8px;
    height: 200px;
    margin: 0 0 6px;

    &::before {
        content: '';
        position: absolute;
        bottom: 32px;
        left: 24px;
        right: 24px;
        height: 2px;
        background: #B1B9D6;
        z-index: -1;
    }
}

.signWrapError {
    border-color: $red;
}

.signPad {
    width: 100%;
    height: 100%;
}

.signTitle {
    font-weight: 700;
    font-size: 14px;
    text-transform: capitalize;
    color: $gray;
    position: absolute;
    top: 24px;
    left: 24px;
    pointer-events: none;
}

.signHint {
    color: $blue;
    font-weight: 500;
    font-size: 12px;
}

.esignFooter {
    display: flex;
    justify-content: space-between;
    margin-bottom: 32px;
}

.clearBtn {
    cursor: pointer;
    font-size: 12px;
    color: $light-gray;
    border: 0;
    outline: none;
    background: none;
    padding: 0;
    text-transform: uppercase;
    text-decoration: underline;
    transition: opacity $animation-speed ease-out;

    &:hover {
        opacity: .85;
    }

    @media screen and (max-width: $phone) {
       margin-bottom: 5px;
    }
}

.titleField {
    // margin-bottom: 8px;
    // PixelPerfect mockup should be:
    margin-bottom: 18px;
}

.titleFieldWithLabel {
    // margin-bottom: 12px;
    // PixelPerfect mockup should be:
    margin-bottom: 24px;
}

.categoryAnswer {
    color: $gray;
    font-size: 14px;
    font-style: italic;
    line-height: 24px;
}

.label {
    margin-bottom: 12px;
}

.subtitle {
    margin-top: 10px;
}
