@import '../../../styles/base/variables';

.button {
    border-radius: 8px;
    padding: 16px 10px;
    min-width: 247px;
    text-transform: uppercase;
    outline: none;
    cursor: pointer;
    transition: $animation-speed opacity ease-out;
    text-align: center;
    border: 1px solid $blue;
    font-weight: 600;
    font-size: 16px;

    &:hover {
        opacity: .85;
    }

    &[disabled] {
        opacity: .2;
        cursor: not-allowed;
    }

    @media screen and (max-width: $tablet) {
        min-width: 190px;
        padding: 15px;
        font-size: 14px;
    }
}

.arrow {
    margin-left: 15px;
}

.arrowBack {
    margin-right: 15px;
    svg {
        transform: rotate(180deg);
    }
}

.primary {
    background: $blue;
    color: $white;
}

.secondary {
    background: $white;
    color: $blue;
}