@import '../../../styles/base/variables';

.medium {
    font-weight: 600;
    font-size: 18px;
    color: $gray-200;
    line-height: 26px;
    letter-spacing: -0.01em;

    @media screen and (max-width: $tablet) { 
        font-size: 17px;
    } 
}

.tooltip {
    position: relative;
    top: -3px;
    @media screen and (max-width: $tablet) {        
        top: 0px;
    } 
    left: 7px;
}

.tooltipMedium {
    display: inline-block;
    width: 15px;
    height: 20px;
    top: 1px;
    left: 5px;

    svg {
        width: 100%;
    }
}