html {
    scroll-behavior: smooth;
}

body {
    color: $black;
    font-family: $base-font-sans-serif;
}

.container {
    padding: 0 15px;
}

.field {
    margin: 0 0 26px;
}

.fieldsRow {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -15px
}

.fieldsCol {
    padding: 0 15px;
}

.cityStateRow {
    .cityStateCol {
        width: 33%;
        @media screen and (max-width: $tablet) {
            width: 100%;
        }
    }
}

.namesRow {
    .namesCol {
        width: 50%;
        @media screen and (max-width: $tablet) {
            width: 100%;
        }
    }
}

.addressesRow {
    .addressesCol {
        width: 50%;
        @media screen and (max-width: $tablet) {
            width: 100%;
        }
    }
}

.hidden {
    opacity: 0;
    visibility: hidden;
}