@import '../../styles/base/variables';

.reviewAnswerTitleWrap {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    
    @media screen and (max-width: $tablet) {
        margin-bottom: 7px;

        button {
            margin-left: 0px;
            margin-top: -5px;
            padding-left: 35px;
        }
    }     
}

.reviewAnwerTitle {
    font-size: 16px;
    color: $black;
    margin: auto 0px;
    @media screen and (max-width: $tablet) {
        font-size: 14px;
    } 
}
