@import '../../../styles/base/variables';

.formPageWrap {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.formContainer {
    display: flex;
    flex: 1 0 auto;
    margin: 60px;
    border: 1px solid $light-blue-300;
    border-radius: 16px;
    overflow: hidden;
    max-height: 692px;
    position: relative;

    @media screen and (max-width: $desktop) {
        max-height: none;
        flex-wrap: wrap;
        margin: 30px;
        flex: none;
    }

    @media screen and (max-width: $tablet) {
        margin: 15px;
    }
}

.esignForm {
    max-height: 935px;
    @media screen and (max-width: $desktop) {
        max-height: none;
    }
}

.questionsListTitleWrap {
    position: relative;
    display: flex;
    width: 100%;
    max-width: 301px;

    @media screen and (max-width: $desktop) {
        max-width: none;
    }

    &:before {
        content: '';
        pointer-events: none;
        max-width: 301px;
        height: 100%;
        max-height: 63px;
        position: absolute;
        left: 0;
        top: 0;
        right: $width-scrollbar;
        z-index: 5;
        background: linear-gradient(180deg, #FBFCFF 30%, rgba(251, 252, 255, 0) 100%);
        border-left: 1px solid $light-blue-300;
        transition: .3s max-height ease-out;

        @media screen and (max-width: $desktop) {
            display: none;
        }
    }

    &:after {
        content: '';
        pointer-events: none;
        max-width: 301px;
        height: 100%;
        max-height: 63px;
        position: absolute;
        left: 0;
        bottom: 0;
        right: $width-scrollbar;
        z-index: 5;
        background: linear-gradient(180deg, rgba(251, 252, 255, 0) 0%, #F7F9FD 100%, #FBFCFF 100%);
        border-left: 1px solid $light-blue-300;
        transition: .3s max-height ease-out;

        @media screen and (max-width: $desktop) {
            display: none;
        }
    }

    h4 {
        position: absolute;
        left: 24px;
        top: 40px;
        z-index: 10;
        background: $light-blue;

        @media screen and (max-width: $desktop) {
            left: 20px;
            top: 20px;
        }

        @media screen and (max-width: $tablet) {
            left: 15px;
            top: 15px;
        }
    }
}

.upperGradienOn {
    &::before {
        max-height: 200px;
    }
}

.downGradientOn {
    &::after {
        max-height: 200px;
    }
}


.questionsListWrap {
    width: 100%;
    background: $light-blue;
    border-left: 1px solid $light-blue-300;
    overflow-y: auto;

    @media screen and (max-width: $desktop) {
        border-left: 0;
        border-top: 1px solid $light-blue-300;
    }

    .questionsListContent {
        padding: 81px 24px 40px;

        @media screen and (max-width: $desktop) {
            padding: 60px 20px 20px;
            overflow: hidden;
        }

        @media screen and (max-width: $tablet) {
            padding: 50px 15px 15px;
        }
    }
}

.currentQuestionWrap {
    width: 100%;
    padding: 40px 43px 40px 50px;
    display: flex;
    flex-direction: column;
    overflow-y: auto;

    @media screen and (max-width: $desktop) {
        padding: 40px 25px
    }

    @media screen and (max-width: $tablet) {
        padding: 40px 20px;
    }
}

.questionContainer {
    padding: 36px 0 0;
}

.responsesList {
    margin: 26px 0;

    li {
        margin: 0 0 10px;
    }
}

.questionTitle {
    margin-bottom: 10px;
}

.questionSubTitle {
    color: $gray;
    margin: 0 0 26px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
}

.questionFormUI {
    margin: 26px 0 25px;
}

.formFooter {
    display: flex;
    justify-content: space-between;
    margin: auto 0 0;
    flex-wrap: wrap;

    @media screen and (max-width: $phone) {        
        button {
            min-width: 48%;
            width: 48%;
        }        
    }

    button {
        margin-top: 15px;
    }
}

