.reviewMainTitle {
    font-size: 40px;
    color: $black;
    margin-bottom: 10px;

    @media screen and (max-width: $tablet) {
        font-size: 36px;
    }
}

.reviewContent {
    padding-top: 54px;
}

.reviewAnswerContent {
    margin-bottom: 20px;
    padding: 15px;
    border: 1px solid #D1D4DE;
    border-radius: 14px;
        
    @media screen and (max-width: $tablet) {
        padding: 15px;
    }
}

.debtorsBox {
    &:not(:last-of-type) {
        margin-bottom: 20px;
    }
}

.displayNone {
    display: none;
}

.jusC {
    justify-content: center;
}

.fullWidth {
    @media screen and (max-width: $phone) {        
        width: 100%!important;
        min-width: 100%!important;
    }
