// Colors:
$black: #1D1D20;
$black-200: #45464B; 
$white: #ffffff;
$gray: #66666C;
$gray-200: #505158;
$gray-300: #3D3E45;
$light-gray:  #737D91;
$blue:  #031C77;
$light-blue  : #FBFCFF;
$light-blue-200 : #EAF1FF; 
$light-blue-300: #DADDE5; 
$placeholder-color: $gray;
$modal-header-border: #D7E3E4;
$red: #D86464;
$green: #11AF22;

// Fonts:
$base-font-sans-serif: 'Arimo';
$base-font-serif: 'Arimo';

$rem-base: 16px;

// Headers
$h1-font-size: 32px;
$h2-font-size: 26px;
$h3-font-size: 24px;
$h4-font-size: 22px;
$h5-font-size: 19px;
$h6-font-size: 16px;

$headings-font-family: $base-font-sans-serif;
$headings-color: inherit !default;

// Animation
$animation-speed: 0.3s;


// Screens:
$phone: 480px;
$tablet: 768px;
$desktop: 1024px;
$widescreen: 1440px;

// Scrollbar:
$width-scrollbar: 2px;
$height-scrollbar: 2px;