.formSubmitWrap {
    padding: 60px;
    background-color: #FBFCFF;
    height: 100vh;

    @media screen and (max-width: 768px) {
       padding: 0;
    }
}

.formSubmit {
    background-color: #ffffff;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 12px;
    position: relative;
    overflow: hidden;
    padding: 10px;
}

.LineTop {
    position: absolute;
    top: 0;
    left: 0;
}

.ItemTop {
    position: absolute;
    top: 0;
    right: 0;

    @media screen and (max-width: 480px) {
        display: none;
    }
}

.ItemBottom {
    position: absolute;
    bottom: 0;
    left: 0;

    @media screen and (max-width: 480px) {
        display: none;
    }
}

.LineBottom {
    position: absolute;
    bottom: 0;
    right: 0;
}

.SubmitImg {
    margin-bottom: 40px;

    @media screen and (max-width: 480px) {
        margin-bottom: 20px;
    }
}

.submit_title {
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    text-transform: capitalize;
    color: #1D1D20;
    margin-bottom: 12px;
}

.submit_text {
    font-weight: 500;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    letter-spacing: -0.01em;
    color: #66666C;
}

.mail_link {
    color: #3D3E45;
    text-decoration: none;
}

.submit_btn {
    min-width: 375px;
    margin-bottom: 15px;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    color: #FFFFFF;

    @media screen and (max-width: 480px) {
        min-width: 100%;
    }
}

.main_btn {
    min-width: 375px;

    @media screen and (max-width: 480px) {
        min-width: 100%;
    }
}