@import '../../../styles/base/variables';

.radioBtnWrapCommon {
    cursor: pointer;
    position: relative;
    border: 1px solid $light-blue-300;
    border-radius: 8px;
    transition: $animation-speed border-color ease-out,
    $animation-speed background-color ease-out;
    background: transparent;
    text-align: left;
    width: 100%;
}

.radio {
    position: absolute;
    border-radius: 50%;
    border: 1px solid;
    transition: $animation-speed border-color ease-out;

    svg {
        position: absolute;
        left: -1px;
        top: -1px;
        width: inherit;
        height: inherit;
    }
}

.radioBtnWrapMedium {
    padding: 15px;

	@media screen and (max-width: $tablet) {
		padding: 15px;
	}

    &:hover {
        background-color: $light-blue;
        border-color: $blue;

        .radio {
            border-color: $blue;
        }
    }

    .label {
        color: $black;
        font-size: 18px;
        padding: 0 0 0 34px;
        margin: 0;

        @media screen and (max-width: $tablet) {
            font-size: 17px;
        }
    }

    .radio {
        width: 20px;
        height: 20px;
        top: 15px;
        left: 15px;
        border-color: $light-gray;

        @media screen and (max-width: $tablet) {
            top: 15px;
            left: 15px;
        }
    }
}

.radioBtnWrapSmall {
    padding: 10px;
    background:  $light-blue;
    // cursor: initial;

    &:hover {
        border-color: $blue;
    }

    .label {
        color: $blue;
        font-size: 14px;
        padding: 0 0 0 24px;
        font-weight: 500 ;
        margin: 0;
    }

    .radio {
        top: 10px;
        left: 10px;
        width: 16px;
        height: 16px;
        border-color: $blue;
    }
}

.currentRadioBtn {
    border: 1px solid $blue;
}
