@import "../../../styles/base/variables";

.questionSubTitle {
    color: $gray;
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    
    @media screen and (max-width: $tablet) {
        font-size: 16px;
        line-height: 22px;
    }    
}

.tooltip {
    position: relative;
    top: 0px;
    left: 7px;
}

.small {
    font-size: 14px;    
}

.tooltipSmall {
    display: inline-block;
    width: 14px;
    height: 20px;
    top: 5px;
    left: 5px;

    svg {
        width: 100%;
    }
}